"use client"

import { useEffect, useState } from "react"
import { Sidebar } from "@/components/sidebar"
import { Header } from "@/components/header"
import { StatCard } from "@/components/stat-card"
import { DailyRequestsChart } from "@/components/charts/daily-requests-chart"
import { DPDPieChart } from "@/components/charts/dpd-pie-chart"
import { PartnerBreakdown } from "@/components/charts/partner-breakdown"
import { RecentUploads } from "@/components/recent-uploads"
import { useCRMStore } from "@/lib/store"
import { sampleProtectRecords, sampleSettlementRecords, calculateDashboardStats } from "@/lib/sample-data"
import { FileText, Shield, Handshake, Users, TrendingUp, BarChart3 } from "lucide-react"

export default function DashboardPage() {
  const { records, uploadHistory, setRecords } = useCRMStore()
  const [isHydrated, setIsHydrated] = useState(false)

  // Initialize with sample data if empty
  useEffect(() => {
    setIsHydrated(true)
    if (records.length === 0) {
      setRecords([...sampleProtectRecords, ...sampleSettlementRecords])
    }
  }, [records.length, setRecords])

  const stats = calculateDashboardStats(records, uploadHistory)

  if (!isHydrated) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center">
        <div className="animate-pulse text-muted-foreground">Loading...</div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background">
      <Sidebar />
      <main className="ml-64">
        <Header title="Dashboard" subtitle="Overview of your loan settlement operations" />

        <div className="p-6 space-y-6">
          {/* KPI Stats */}
          <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-6">
            <StatCard
              title="Total Requests"
              value={stats.totalRequests}
              subtitle="All records"
              icon={FileText}
              iconColor="text-primary"
              iconBg="bg-primary/10"
            />
            <StatCard
              title="Protect Requests"
              value={stats.totalProtect}
              subtitle="Protection cases"
              icon={Shield}
              iconColor="text-blue-500"
              iconBg="bg-blue-500/10"
            />
            <StatCard
              title="Settlement Requests"
              value={stats.totalSettlement}
              subtitle="Settlement cases"
              icon={Handshake}
              iconColor="text-green-500"
              iconBg="bg-green-500/10"
            />
            <StatCard
              title="Sayyam Total"
              value={stats.sayyamProtect + stats.sayyamSettlement}
              subtitle={`${stats.sayyamProtect} P / ${stats.sayyamSettlement} S`}
              icon={Users}
              iconColor="text-purple-500"
              iconBg="bg-purple-500/10"
            />
            <StatCard
              title="Snapmint Total"
              value={stats.snapmintProtect + stats.snapmintSettlement}
              subtitle={`${stats.snapmintProtect} P / ${stats.snapmintSettlement} S`}
              icon={TrendingUp}
              iconColor="text-orange-500"
              iconBg="bg-orange-500/10"
            />
            <StatCard
              title="Recent Uploads"
              value={stats.recentUploads.length}
              subtitle="Files processed"
              icon={BarChart3}
              iconColor="text-teal-500"
              iconBg="bg-teal-500/10"
            />
          </div>

          {/* Charts Row */}
          <div className="grid grid-cols-1 gap-4 lg:grid-cols-4">
            <DailyRequestsChart data={stats.dailyRequests} />
            <DPDPieChart data={stats.dpdDistribution} />
            <PartnerBreakdown
              sayyamProtect={stats.sayyamProtect}
              sayyamSettlement={stats.sayyamSettlement}
              snapmintProtect={stats.snapmintProtect}
              snapmintSettlement={stats.snapmintSettlement}
            />
          </div>

          {/* Recent Uploads */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
            <RecentUploads uploads={stats.recentUploads} />
          </div>
        </div>
      </main>
    </div>
  )
}
